* {
  scroll-behavior: smooth;
}

html {
  background-color: whitesmoke;
}

.app-container {
  display: flex;
  flex-direction: row;
  height: 100vh;
  border: 10px white solid;
}

.content-container {
  width: 100%;
  overflow: scroll;
  display: flex;
  flex-direction: column;
}

.section-title {
  margin: 0.5em 0 0.2em;
  font-size: 3em;
  font-family: "Poppins", sans-serif;
  color: rgb(255, 255, 255);
}

/* ---- Misc ---- */

.highlight {
  color: rgb(15, 179, 116);
}